import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Http, Response } from '@angular/http';
/**
 * Generated class for the AttentionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-attention',
  templateUrl: 'attention.html',
})
export class AttentionPage {
  news: String = "toutiao";
  newsMap: Object[] = [
  {value:"toutiao",name: "头条"},
  {value:"auto",name: "汽车"},
  {value:"money",name: "财经"},
  {value:"sports",name: "运动"},
  {value:"dy",name: "电影"},
  {value:"war",name: "军事"},
  {value:"ent",name: "娱乐"},
  {value:"tech",name: "教育"}
  ];
	picData: Object = {
    code: undefined,
    msg: "",
    data: []
  };
  newsData:Object = {
    code: undefined,
    msg: "",
    data: []
  };
  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	private http: Http
  	) {
    
  }
  updatePicData(){
    this.http.request('https://www.apiopen.top/meituApi?page=1')
    .subscribe((res: Response) => {
      this.picData = res.json();
      console.log(this.picData);
    }); 
  }
  updateNewsData(){
    this.http.request('https://www.apiopen.top/journalismApi')
    .subscribe((res: Response) => {
      this.newsData = res.json();
      console.log(this.newsData);
    }); 
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AttentionPage');
    this.http.request('https://www.apiopen.top/journalismApi')
    .subscribe((res: Response) => {
      this.newsData = res.json();
      console.log(this.newsData);
    }); 
  }

}



